.footer {
  height: 50px;
  display: flex;
  border-top: $border;
  &__title {
    line-height: 50px;
    width: 195px;
    border-right: $border;
    padding: 0 22px;
    @media (max-width: 390px) {
      padding: 0 15px;
    }
  }
  &__link {
    display: flex;
    align-items: center;
    padding: 17px;
    border-right: $border;
    text-decoration: none;
    color: inherit;
    svg {
      height: 25px;
    }
    &:last-child {
      margin-left: auto;
      border: none;
      border-left: $border;
      svg {
        margin-left: 10px;
        @media (max-width: 600px) {
          margin-left: 0;
        }
      }
      &:before {
        content: '@nicolasolenev';
        @media (max-width: 600px) {
          content: '';
        }
      }
    }
    &:hover {
      cursor: pointer;
      color: $hover-color;
      transition: all 0.3s;
    }
  }
}
