.content {
  &__cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin: 90px auto;
  }

  &__card {
    width: 370px;
    height: 340px;

    .project-title {
      span {
        color: #5565e8;
      }
      margin-bottom: 15px;
    }

    .project-content {
      border: 1px solid #1e2d3d;
      border-radius: 15px;
    }

    .project-desc {
      display: block;
      margin-top: 180px;
      padding: 0 30px;
    }

    .project-refer {
      display: inline-block;
      background: #1c2b3a;
      color: #fff;
      text-decoration: none;
      border-radius: 8px;
      padding: 10px 14px;
      margin: 31px;
    }
  }
}

.lazarevafoto {
  background: url('../../../public/lazarevafoto-2.png') top no-repeat;
  background-size: contain;
}
