@import url('https://fonts.googleapis.com/css2?family=Fira+Code:wght@300;400&display=swap');

* {
  box-sizing: border-box;
}

body {
  display: flex;
  height: 100vh;
  margin: 0;
  background-color: #010c15;
  color: #607b96;
  font-family: 'Fira Code', monospace;
  font-size: 16px;
  line-height: 21px;

  #root {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    width: 100%;
  }
}
