.nav-bar-btn {
  display: none;
  border: none;
  background: transparent;
  width: 60px;
  height: 60px;
  margin-left: auto;
  cursor: pointer;
  @media (max-width: 1000px) {
    display: block;
    position: relative;
    &__line,
    &__line::before,
    &__line::after {
      position: absolute;
      top: 50%;
      left: 50%;
      margin: -1px 0 0 -9px;
      width: 18px;
      height: 2px;
      background: $main_text_color;
    }
    &__line::before {
      content: '';
      transform: translateY(-5px);
      transition: .3s;
    }
    &__line::after {
      content: '';
      transform: translateY(5px);
      transition: .3s;
    }
    &_active {
      .nav-bar-btn__line {
        background: transparent;
      }
      .nav-bar-btn__line::before {
        transform: rotate(-45deg);
      }
      .nav-bar-btn__line::after {
        transform: rotate(45deg);
      }
    }
  }
}