@import './nav-bar-btn';

@mixin underline($visability) {
  background-image: linear-gradient($active-tab-color, $active-tab-color);
  background-position: 0% 100%;
  background-repeat: no-repeat;
  background-size: $visability 3px;
  transition: all 0.3s;
}

.header {
  height: 60px;
  width: 100%;
  display: flex;
  border-bottom: $border;
  position: relative;

  &__title {
    width: 311px;
    line-height: 60px;
    border-right: $border;
    padding: 0 0 0 22px;
    @media (max-width: 1000px) {
      border: none;
      width: 135px;
    }
  }

  &__menu {
    display: flex;
    flex-grow: 1;
    z-index: 10;
    @media (max-width: 1000px) {
      position: absolute;
      top: 60px;
      right: -105%;
      width: 100%;
      height: calc(96vh - 111px);
      align-items: start;
      background-color: #011627;
      transition: right 0.2s ease-in;
      &_active {
        transition: right 0.3s ease-out;
        right: 0;
      }
    }
  }

  &__links {
    margin: 0;
    padding: 0;
    display: flex;
    flex-grow: 1;
    @media (max-width: 1000px) {
      flex-flow: row wrap;
    }
  }

  &__link {
    list-style: none;
    border-right: $border;
    display: flex;
    align-items: center;
    &:last-child {
      margin-left: auto;
      border-left: $border;
      border-right: none;
    }
    a {
      display: block;
      padding: 0 32px;
      line-height: 60px;
      color: inherit;
      text-decoration: none;
      @include underline(0%);
      &.active {
        color: $hover-color;
        background-image: linear-gradient($active-tab-color, $active-tab-color);
        background-position: 0% 100%;
        background-repeat: no-repeat;
        background-size: 100% 3px;
      }
      &:hover {
        cursor: pointer;
        color: $hover-color;
        @include underline(100%);
      }
      @media (max-width: 1000px) {
        width: 100%;
        padding: 0 18px;
      }
    }
    @media (max-width: 1000px) {
      border: none;
      border-bottom: $border;
      width: 100%;
      height: 55px;
      &:last-child {
        border-left: none;
      }
      &:first-child {
        border-top: $border;
      }
    }
  }
}
