.main {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 35px auto;

  &__wrapper {
    @media (max-width: 730px) {
      margin: 0 27px;
    }
  }

  &__title {
    position: relative;
    font-size: 62px;
    line-height: 81px;
    color: #e5e9f0;
    margin: 35px 130px 0 0;
    @media (max-width: 730px) {
      margin: 0;
    }
    &:before {
      content: 'Hi all. I am';
      font-size: 18px;
      line-height: 24px;
      position: absolute;
      top: -20px;
      left: 4px;
    }
  }

  &__subtitle {
    display: block;
    color: #4d5bce;
    font-size: 32px;
    line-height: 42px;
    @media (max-width: 730px) {
      color: #43d9ad;
      font-size: 20px;
    }
  }
  &__skills {
    margin-top: 90px;
    ul {
      margin: 0;
      padding: 0 0 0 10px;
    }
    li {
      list-style: none;
      &:before {
        content: '//';
        position: relative;
        left: -10px;
      }
    }
  }
}
