.game {
  width: 510px;
  height: 470px;
  display: flex;
  justify-content: center;
  background: linear-gradient(
    150.26deg,
    rgba(23, 85, 83, 0.7) 1.7%,
    rgba(67, 217, 173, 0.091) 81.82%
  );
  border: 1px solid #0c1616;
  box-shadow: inset 0px 2px 0px rgba(255, 255, 255, 0.3);
  backdrop-filter: blur(64px);
  border-radius: 8px;
  position: relative;
  @media (max-width: 1220px) {
    display: none;
  }

  &__field {
    width: 240px;
    height: 400px;
  }

  &__cell {
    width: 8px;
    height: 8px;
    background: #fff;
  }

  .bolt-up-left {
    background: radial-gradient(
      73.08% 73.08% at 50% 26.92%,
      #196c6a 15.1%,
      #114b4a 100%
    );
    box-shadow: 2px 2px 4px #0d4341, inset 0px 1px 2px #1a8277;
    top: 13px;
    left: 13px;
  }

  .bolt-up-right {
    background: radial-gradient(
      73.08% 73.08% at 50% 26.92%,
      #196c6a 15.1%,
      #114b4a 100%
    );
    box-shadow: 2px 2px 4px #0d4341, inset 0px 1px 2px #1a8277;
    top: 13px;
    right: 13px;
  }

  .bolt-down-left {
    background: radial-gradient(
      73.08% 73.08% at 50% 26.92%,
      #164c51 15.1%,
      #0d3a40 100%
    );
    box-shadow: 2px 2px 4px #092d32, inset 0px 1px 2px #16645c;
    bottom: 13px;
    left: 13px;
  }

  .bolt-down-right {
    background: radial-gradient(
      73.08% 73.08% at 50% 26.92%,
      #234b7c 15.1%,
      #122e4f 100%
    );
    box-shadow: 2px 2px 4px #122c4e, inset 0px 1px 2px #2c588d;
    bottom: 13px;
    right: 13px;
  }

  &__wrapper {
    margin: 35px 30px;
    height: 400px;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }

  &__window {
    width: 239px;
    height: 400px;
    background: rgba(1, 22, 39, 0.84);
    box-shadow: inset 1px 5px 11px rgba(2, 18, 27, 0.71);
    border-radius: 8px;
    position: relative;
    overflow: hidden;
  }

  &__helper {
    width: 182px;
    position: relative;
  }

  &__instructions {
    background: rgba(1, 20, 35, 0.19);
    border-radius: 8px;
    height: 144px;
    ul {
      margin: 0 auto;
      padding: 15px 13px 15px 23px;
    }
  }

  &__instruction {
    list-style: none;
    font-size: 14px;
    line-height: 18px;
    color: #fff;
    &::before {
      content: '//';
      position: relative;
      left: -10px;
    }
  }

  &__arrows {
    display: flex;
    flex-direction: column;
    align-items: center;
    &_bottom {
      display: flex;
      flex-wrap: wrap;
    }
  }

  &__arrow {
    width: 49px;
    height: 28px;
    background: #010c15;
    border: 1px solid #1e2d3d;
    border-radius: 8px;
    margin: 2px;
    span {
      display: block;
      background: transparent;
      border-radius: 10px;
      padding: 20px;
      position: relative;
      &::after {
        content: '';
        position: absolute;
        left: 20px;
        top: 12px;
        border: 4px solid transparent;
        border-top: 6px solid #fff;
      }
    }
    &_up {
      span {
        transform: rotate(180deg);
        &::after {
          top: 23px;
        }
      }
    }

    &_left {
      span {
        padding: 15px;
        transform: rotate(90deg);
        &::after {
          top: 13px;
        }
      }
    }

    &_right {
      span {
        padding: 14px;
        transform: rotate(-90deg);
        &::after {
          top: 12px;
        }
      }
    }
  }

  &__food-left {
    margin: 21px 0 0 0;
    .game__instruction {
      display: block;
      margin: 0 0 11px 0;
      padding: 0 0 0 23px;
    }
  }

  &__food-left-items {
    width: 155px;
    ul {
      display: flex;
      flex-wrap: wrap;
      padding: 0;
      margin: 0 0 0 12px;
    }
  }

  &__food-left-item {
    display: block;
    width: 21px;
    height: 21px;
    background: url('./food-left.svg') no-repeat center;
    list-style: none;
    margin: 0 7px 7px 0;
  }

  &__food-left-item-done {
    background: url('./food-left-done.svg') no-repeat center;
  }

  &__skip {
    display: inline-block;
    font-size: 14px;
    line-height: 38px;
    color: #fff;
    width: 62px;
    border: 1px solid #ffffff;
    border-radius: 8px;
    background: transparent;
    position: absolute;
    bottom: 0;
    right: 0;
    cursor: pointer;
    font-family: inherit;
    text-decoration: none;
    text-align: center;
  }

  &__start {
    background: #fea55f;
    border-radius: 8px;
    width: 112px;
    height: 38px;
    border: none;
    font-family: inherit;
    font-size: 14px;
    position: absolute;
    left: calc(50% - 56px);
    bottom: 55px;
    cursor: pointer;
  }

  &__start-again {
    background: transparent;
    border: none;
    color: #607b96;
    font-size: 14px;
    cursor: pointer;
  }
}

.bolt {
  width: 13px;
  height: 13px;
  border-radius: 50%;
  position: absolute;
  &__line-1,
  &__line-2 {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -0.5px 0 0 -3.5px;
    width: 7px;
    height: 1px;
    background: #093430;
  }

  &__line-1 {
    transform: rotate(40deg);
  }

  &__line-2 {
    transform: rotate(-40deg);
  }
}

//Snake

.grid {
  // we don't want borders to factor into the size, so that grid calculations can be exact, so use content-box sizing
  box-sizing: content-box;
  padding: 0;
  // border-top: 1px solid lightgray;
  // border-right: 1px solid lightgray;
  display: flex;
  flex-wrap: wrap;
}

.grid-cell {
  // border-left: 1px solid lightgray;
  // border-bottom: 1px solid lightgray;
  transition: 0.1s;
  &--food {
    // background-color: blue;
    background: url('./food-left.svg') center;
  }

  &--snake {
    background-color: #43d9ad;
  }
}

.snake-app {
  position: relative;
  // margin: 10px auto;
  &:focus {
    outline: none;
  }
}

.snake-app__overlay {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  // background-color: hsla(0, 0, 0, 0.9);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  color: white;
  font-size: 1.2rem;
  margin-bottom: 50px;
}

.mb-1 {
  margin-bottom: 1rem;
  width: 100%;
  text-align: center;
  line-height: 48px;
}

.mb-status {
  background: rgba(1, 22, 39, 0.84);
  box-shadow: inset 1px 5px 11px rgba(2, 18, 27, 0.71);
  border-radius: 8px;
  height: 48px;
}

