.content-wrapper {
  display: flex;
  height: 100%;
}

.left-side-bar {
  width: 311px;
  border-right: $border;
  &__nav ul {
    list-style: none;
    padding-left: 34px;
    li {
      height: 30px;
      &:hover {
        cursor: pointer;
        text-decoration: underline;
      }
    }
  }
  &__title {
    color: #fff;
    padding: 9px 10px 9px 34px;
    border-bottom: $border;
    border-top: $border;
    position: relative;
    cursor: pointer;
    &:first-child {
      border-top: none;
    }
    &::after {
      content: '';
      position: absolute;
      width: 9px;
      height: 7px;
      display: block;
      left: 14px;
      top: 17px;
      background: url('../../../public/list-triangle.svg');
    }
  }
  &__bio,
  &__interests,
  &__education,
  &__mail,
  &__phone {
    position: relative;
    padding: 0 0 0 22px;
    &::after {
      content: '';
      position: absolute;
      width: 16px;
      height: 14px;
      display: block;
      left: 0px;
      top: 2px;
    }
  }

  &__bio:after {
    background: url('../../../public/folder-pink.svg') no-repeat;
  }

  &__interests:after {
    background: url('../../../public/folder-green.svg') no-repeat;
  }

  &__education:after {
    background: url('../../../public/folder-purple.svg') no-repeat;
  }

  &__mail:after {
    background: url('../../../public/mail-icon.svg') no-repeat;
    width: 16px;
    height: 17px;
  }

  &__phone:after {
    background: url('../../../public/phone-icon.svg') no-repeat;
    width: 18px;
    height: 19px;
  }
}

.content {
  flex-grow: 1;
  background: #011627;

  &__title {
    border-bottom: $border;
  }

  &__tab {
    display: inline-block;
    border-right: $border;
    padding: 0 80px 0 15px;
    line-height: 39px;
    position: relative;
  }

  &__close-btn {
    display: inline-block;
    position: absolute;
    width: 40px;
    height: 40px;
    right: 0;
    background: url('../../../public/close-icon.svg') no-repeat center;
    cursor: pointer;
  }
}
